<template>
  <div id="app">
    <div class="bg" :style="{backgroundImage:'url(' + backc + ')'}"></div>
    <div class="content">
      <transition name="el-fade-in-linear">
        <div v-if="isShow" class="transition-box await">
          <p class="Welcomes">{{Welcomes}}</p>
          <p class="Starting">{{Starting}}</p>
        </div>
      </transition>

      <div class="system" v-if="false">
        <div class="posi-rit">
          <div class="wifi">
            <i :class="system.wifi"></i>
          </div>
          <div class="battery">
            <i class="iconfont icon-iconset0248"></i><i v-if="system.ifCharg" class="iconfont icon-icon-test"></i><div>{{system.batteryNums}}</div>
            <div v-if="system.isOver" class="over"></div>
          </div>
        </div>
      </div>

      <div class="child">
        <!-- <transition name="transition-box await"> -->
          <router-view></router-view>
        <!-- </transition> -->
      </div>

    </div>
  

  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      // backc:encodeURI('http://p2.music.126.net/tt8xwK-ASC2iqXNUXYKoDQ==/109951163606377163.jpg'),
      backc:'',
      isShow:true,
      Welcomes:"",
      Starting:"",
      system:{
        wifi:"",
        batteryNums:"0%",
        ifCharg:false,
        isOver:false
      },
    }
  },
  watch:{
    '$store.state.musicPic'(){//soket 更新内容
      this.backc = this.$store.state.musicPic
    }
     
  },
  mounted() {
    // this.getSystem();
    // this.setSystem();
    if(localStorage.getItem("msg")){
      
    }else{
      this.Welcomes="6666";
      this.Starting="魔镜系统正在启动中...";
    }
    // if(this.$store.state.musicPic){
    //   this.backc = this.$store.state.musicPic
    // }
    setTimeout(() => {
      this.isShow = false
    }, 3000);
  },
  methods:{
    getSystem(){   //获取系统信息
      let that = this;
      if(navigator.userAgent.indexOf('iPhone')>-1){
     

     
      }else{
        navigator.getBattery().then(function(battery) {
          that.system.batteryNums = parseInt(battery.level * 100) + "%"  //当前点电量
          if(battery.charging == true){
            that.system.ifCharg = true;
            that.system.isOver = true
          }else{

          }
        });

        if(navigator.onLine == true){  //网络是否连接
          that.system.wifi="iconfont icon-WIFIwofi"
        }else{
          that.system.wifi="iconfont icon-wifiweilianjie"
        }

      }
      
    },
    setSystem(){
      let that = this ;
      setInterval(function() {
        that.getSystem();
      },1000)
    }
  }
}
</script>

<style lang="less">
*{
  margin: 0;
  padding: 0;
}
body,html{
  width: 100%;
  height: 100%;
  font-size: 6.25%; /*10 ÷ 16 × 100% = 62.5%*/
}
#app {
  // font-family:"草书";//楷体
  // height: 100%;
  width: 100%;
  height: 100%;
  background-color: #000;
  // background-size:100% 100%;
  // -moz-background-size:100% 100%;
  .bg{
    width: 100%;
    height: 100%;

    background-repeat:no-repeat;
    background-size: cover;
    -webkit-filter: blur(15px);
    -moz-filter: blur(15px);
    -o-filter: blur(15px);
    -ms-filter: blur(15px);
    filter: blur(15px);

    -webkit-transition:all 1.0s ease-in-out;
    -moz-transition:all 1.0s ease-in-out;
    -o-transition:all 1.0s ease-in-out;
    -ms-transition:all 1.0s ease-in-out;
    transition:all 1.0s ease-in-out;
    // opacity: 1;
    position: absolute;
    top: 0;
    left: 0;
  }

  .await{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    color:#eee;
    background-color: black;
    z-index: 99999999999;
    .Welcomes{
      width: 200px;
      height: 80px;
      text-align: center;
      position: absolute;
      top:0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
    .Starting{
      width: 200px;
      height: 80px;
      text-align: center;
      position: absolute;
      // top:0;
      left: 0;
      right: 0;
      bottom: 50px;
      margin:auto;
    }
  }
  .system{
    width: 100%;
    height: .6rem;
    float: right;
    position: relative;
    z-index: 99999999;
    color: #eee;
    .posi-rit{
      width: 2.04rem;
      position: absolute;
      top: 0;
      right: 0;
      .iconfont{
        font-size: .37rem;
      }
      div{
        display: inline-block;
      }
      .wifi{
        width: .37rem;
        height: .43rem;
      }
      .battery{
        height: .74rem;
        line-height: .74rem;
        .icon-iconset0248{
          font-size: .41rem;
        }
        .icon-icon-test{
          font-size: .24rem;
          position: absolute;
          top: .02rem;
        }
        div{
          position: absolute;
          left: 1.11rem;
          top: -.02rem;
        }
        .over{
          width: .29rem;
          height: .136rem;
          background: greenyellow;
          position: absolute;
          left: .49rem;
          top: .28rem;
        }
      }
    }
  }
  .child{
    display: block;
    padding-left: .42rem;
    padding-right: .42rem;
    padding-top: .42rem;
  }
}

</style>



